    padding: 5rem 0rem 5rem 0rem;
}
.user-details-container h4{
    margin-left: 5rem;
}
.user-details-container p{
 margin: .5rem .5rem 0rem 5rem;
 font-family: var(--sans);
 font-size: 18px;
}